.main-nav {
  background-color: #303f9f;
  border: none;
  border-radius: 0px;
}
.main-nav .navbar-brand {
  color: #ffc107;
  font-size: 24px;
}
.main-nav .navbar-brand:hover {
  color: #d39e00;
}
.main-nav .navbar-nav li a {
  color: #ffc107;
}
.main-nav .navbar-nav li a:hover {
  color: #d39e00;
}
.header-text {
  border-bottom: 3px solid #b6b6b6;
  color: #727272;
}
.dashboard-tabs {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dashboard-tabs .tab {
  max-height: 350px;
  background-color: #3f51b5;
  float: left;
  min-height: 1px;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  box-shadow: 5px 5px 10px #888;
  border: 1px solid #3849a2;
  user-select: none;
}
.dashboard-tabs .tab:hover {
  margin-top: -5px;
  background-color: #3b4caa;
}
.dashboard-tabs .tab .tab-icon {
  float: left;
}
.dashboard-tabs .tab .tab-icon .icon-circle {
  background-color: #ffc107;
  border-radius: 50%;
  width: 5em;
  height: 5em;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dashboard-tabs .tab .tab-icon .icon-circle i {
  font-size: 2.5em;
  color: #fff;
}
.dashboard-tabs .tab .tab-text {
  float: right;
  color: #fff;
  font-size: 3em;
}
body {
  font-family: 'Bree Serif', serif;
  background-color: #eaecf7;
}
